import React, { useEffect, useState } from 'react';
import Sidebar from '../components/Sidebar';
import { getReservationHistory } from '../services/reservation';
import jsPDF from 'jspdf';

const Historico: React.FC = () => {
  const [history, setHistory] = useState<any[]>([]);
  const [filteredHistory, setFilteredHistory] = useState<any[]>([]);
  const [error, setError] = useState('');
  const [search, setSearch] = useState('');

  useEffect(() => {
    const fetchHistory = async () => {
      try {
        const data = await getReservationHistory(); // Chamada ao serviço mockado
        setHistory(data);
        setFilteredHistory(data); // Inicializa com todos os dados
      } catch (err) {
        setError('Erro ao carregar o histórico de reservas.');
      }
    };

    fetchHistory();
  }, []);

  // Função para filtrar as reservas com base na pesquisa
  const handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    const query = e.target.value.toLowerCase();
    setSearch(query);
    const filtered = history.filter((item) => {
      return (
        item.user.toLowerCase().includes(query) ||
        item.environment.toLowerCase().includes(query) ||
        item.status.toLowerCase().includes(query)
      );
    });
    setFilteredHistory(filtered);
  };

  // Função para gerar o PDF do histórico
  const generatePDF = () => {
    const doc = new jsPDF();
    doc.setFontSize(18);
    doc.text('Histórico de Reservas', 20, 20);
    doc.setFontSize(12);
    filteredHistory.forEach((item, index) => {
      const y = 30 + index * 10;
      doc.text(`${item.user} reservou o ${item.environment} no dia ${new Date(item.date).toLocaleDateString('pt-BR')} às ${new Date(item.date).toLocaleTimeString('pt-BR', { hour: '2-digit', minute: '2-digit' })}. Status: ${item.status}`, 20, y);
    });
    doc.save('historico_reservas.pdf');
  };

  return (
    <div className="historico-container">
      <Sidebar />
      <div className="historico-content">
        <h1 className="historico-title">Histórico de Reservas</h1>

        {/* Barra de pesquisa */}
        <input
          type="text"
          placeholder="Pesquisar por usuário, ambiente ou status"
          className="search-input"
          value={search}
          onChange={handleSearch}
        />

        {/* Exibição de erro */}
        {error && <p className="error-message">{error}</p>}

        {/* Exibição do botão para gerar PDF */}
        <button onClick={generatePDF} className="pdf-button">Exportar para PDF</button>

        {/* Exibição das reservas */}
        <ul className="reservation-list">
          {filteredHistory.length > 0 ? (
            filteredHistory.map((item: any) => (
              <li key={item.id} className="reservation-item">
                <div className="reservation-info">
                  <strong>{item.user}</strong> reservou o <em>{item.environment}</em> no dia{' '}
                  {new Date(item.date).toLocaleDateString('pt-BR')} às{' '}
                  {new Date(item.date).toLocaleTimeString('pt-BR', { hour: '2-digit', minute: '2-digit' })}.
                  <br />
                  Status: <strong>{item.status}</strong>
                </div>
              </li>
            ))
          ) : (
            <li className="no-results">Nenhuma reserva encontrada.</li>
          )}
        </ul>
      </div>
    </div>
  );
};

export default Historico;
